<template>
  <v-data-table
    :headers="headers"
    :items="desserts"
    :search="search"
    sort-by="description"
    class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>n v</v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <div style="margin-right: 20px">
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Tm kim"
            single-line
            hide-details
          ></v-text-field>
        </div>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
              Thm mi
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12">
                    <v-text-field
                      v-model="editedItem.name"
                      label="Tn n v"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-textarea
                      name="input-7-1"
                      label="Miu t"
                      v-model="editedItem.description"
                      rows="3"
                    ></v-textarea>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close"> Hy </v-btn>
              <v-btn color="blue darken-1" text @click="save"> Lu </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="headline">Chc chn xa?</v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete">Hy</v-btn>
              <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                >OK</v-btn
              >
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:[`item.actions`]="{ item }">
      <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
      <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
    </template>
    <!-- <template v-slot:no-data>
      <v-btn color="primary" @click="initialize"> Reset </v-btn>
    </template> -->
  </v-data-table>
</template>
<script>
export default {
  data() {
    return {
      search: "",
      list: [],
      dialog: false,
      dialogDelete: false,
      headers: [
        {
          text: "Tn n v",
          align: "start",
          value: "name",
        },
        { text: "Miu t", value: "description", sortable: false },
        { text: "Hnh ng", value: "actions", sortable: false },
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        name: "",
        description: "",
      },
      defaultItem: {
        name: "",
        description: "",
      },
    };
  },

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    initialize() {
      this.desserts = this.list;
    },
    getList() {
      this.desserts = [];
      this.CallAPI("get", "unit/list?page=1&limit=99999&order_by=created_at&order_direction=asc", {}, (response) => {
      this.list = response.data.data.data;
      this.desserts = this.list;
    });
    },
    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      let id = this.editedItem.id;
      this.CallAPI("delete", "unit/delete/" + id, {}, (response) => {
        // console.log(response.data);
        if (response.data.code == -1) {
          this.$toast.error("Xa bn ghi khng thnh cng!");
          return;
        }
        if (response.data.code == -100) {
          this.$toast.error("Khng c quyn xa bn ghi!");
          return;
        }
        this.$toast.success("Xa bn ghi thnh cng!");
        this.getList();
        // this.desserts.splice(this.editedIndex, 1);
      });
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (this.editedIndex > -1) {
        let data = {
          name: this.editedItem.name,
          description: this.editedItem.description,
        };
        
        let id = this.editedItem.id;
        if (!data.name || !data.description) {
          this.$toast.error("Vui lng nhp y  thng tin!");
          return;
        }
        this.CallAPI("put", "unit/update/" + id, data, (response) => {
          // console.log(response.data);
          if (response.data.code == -1) {
            this.$toast.error("Cp nht khng thnh cng!");
            return;
          }
          if (response.data.code == -100) {
            this.$toast.error("Khng c quyn sa bn ghi!");
            return;
          }
          this.$toast.success("Cp nht thnh cng!");
          this.getList();
          this.close();
        });
        // Object.assign(this.desserts[this.editedIndex], this.editedItem);
      } else {
        
        let data = {
          name: this.editedItem.name,
          description: this.editedItem.description,
        };
        if (!data.name || !data.description) {
          this.$toast.error("Vui lng nhp y  thng tin!");
          return;
        }
        this.CallAPI("post", "unit/create", data, (response) => {
          // console.log(response.data);
          if (response.data.code == -1) {
            this.$toast.error("Thm bn ghi khng thnh cng!");
            return;
          }
          if (response.data.code == -100) {
            this.$toast.error("Khng c quyn thm bn ghi!");
            return;
          }
          this.$toast.success("Thm bn ghi thnh cng!");
          this.getList();
          // this.desserts.push(this.editedItem);
          this.close();
        });
        
      }
      
    },
  },
  mounted() {
   this.getList();
  },
};
</script>